<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <dynamic-form lang="en" ref="AddJobType" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit"></dynamic-form>
        <div class="row">
            <div class="col-lg-12 text-right">
                <button title="Previous" class="btn btn-warning float-left" @click="PreviousButton()"
                        v-if="currentTabIndex > 1"><i class="fa fa-chevron-left pr-2"></i> {{ $t('Previous') }}</button>                                               
                <button type="button" id="aGroupTicketSave" @click="onSubmit(values)" title=""
                        data-toggle-tooltip="tooltip" data-original-title="Save"
                        class="popupgreenbtn btn btn-success formbtn widthhalf pr-2" style="margin-left: 5px;"
                        v-if="tabCount == currentTabIndex">
                        <i class="fa fa-save pr-2"></i> {{ $t('SaveAndClose') }}
                    </button>
            </div>
        </div>
    </section>
</template>
<script>
import DataService from './../../../../services/AdvanceSetupModuleService'
export default {
    props: {
        tabCount: 0,
        currentTabIndex: 0
    },
    data(){
        return{
            isLoading:false,
            MandatoryString:'Fields marked with an asterisk (*) are mandatory.',
            FormSchema: [{
                    layoutType: "single",
                    Data: [{
                        astype: "TextField",
                        label: this.$t('JobTypeName'),
                        name: "JobTypeName",
                        value: "",
                        placeholder: this.$t('EnterJobTypeName'),
                        validationRules: {
                            "required": true
                        },
                        disabled: false,
                    }, ]
                },
                {
                    layoutType: "single",
                    Data: [{
                        astype: "TextAreaField",
                        label: this.$t('Description'),
                        name: "Description",
                        value: "",
                        placeholder: this.$t('Enter Description'),
                        validationRules: {
                            "required": false
                        },
                    }]
                }
            ],
            buttons: [],
            companyId: null,
            userId: null,
        }
    },
    created: function () {
        var vm = this;
        vm.userId = vm.GetUserInfo.ID;
        vm.companyId = vm.GetUserInfo.CompanyId;        
    },
    methods:{
        PreviousButton: function () {
            var vm = this;
            vm.$emit("previousTab");
        },
        onSubmit() {
            var vm = this;
            vm.$refs.AddJobType.validateForm.validate().then(result => {
                if(result){
                    var jsonobj = {
                        userId: vm.userId,
                        companyId: vm.companyId,
                        name: vm.FormSchema[0].Data[0].value,
                        description: vm.FormSchema[1].Data[0].value,
                        JobTypeId: 0
                    };
                    var params = JSON.stringify(jsonobj);
                    DataService.Saveproducttype(params).then((response) => {                        
                        if (response.data.status == "AlreadyExixts") {                            
                            vm.ShowAlert(vm.$t('AlreadyExists', [vm.$t('AddJobType')]), "warning", true, vm.$t("Alert"));
                        }  
                        if (response.data.Status != "Success") {
                            vm.ShowAlert(vm.$t('DesignationAlreadyExist', [vm.$t('AddJobType')]), "warning", true, vm.$t("Alert"));
                        } else {
                            vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('AddJobType')]), "success", true, vm.$t("Alert"));
                        }                         
                    });
                }
            })
        },
    }
}
</script>