const GetMoreSettingsArray = async function (moduleCode){
    var moreSettings = []
    var Allowprivileges=[]
    const {data}= await setupModuleService.GetAllowedPrivilege()
    Allowprivileges=data
    
    if(moduleCode.toLowerCase() == "ticketing"){
        await setupModuleService.IsExtraMapFeature().then(function (response) {
            moreSettings = moreSettingsArray.filter(function(x){ 
                return (x.moduleName.toLowerCase() == moduleCode.toLowerCase() && typeof(x.visibleTo) != 'undefined' && x.visibleTo.includes(response.data) == true);
            });
        });
        moreSettings=moreSettings.filter(x=>Allowprivileges.some(y=>y.controller==x.controllerName && y.action==x.actionName))
    } else {
        moreSettings = moreSettingsArray.filter(function(x){ 
            return x.moduleName.toLowerCase() == moduleCode.toLowerCase();   
        });
        moreSettings=moreSettings.filter(x=>Allowprivileges.some(y=>y.controller==x.controllerName && y.action==x.actionName))

    }
    return moreSettings;
}

export { moreSettingsArray, generalSettings, GetMoreSettingsArray}