<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <dynamic-form lang="en" ref="FunctionalArea" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="SaveSubmit"></dynamic-form>
        <div class="row">
            <div class="col-lg-12 text-right">
                <button title="Previous" class="btn btn-warning float-left" @click="PreviousButton()"
                            v-if="currentTabIndex > 1"><i class="fa fa-chevron-left pr-2"></i> {{ $t('Previous') }}</button>                                               
                <button title="Skip" class="popupgreenbtn btn skip-btn-clr formbtn widthhalf px-4" @click="Skip()"
                             v-if="tabCount > currentTabIndex">{{ $t("Skip") }}</button>
                <button type="button" id="aGroupTicketSave" title="" data-toggle-tooltip="tooltip" data-original-title="Save" class="popupgreenbtn btn btn-success formbtn widthhalf pr-2"
                             @click="SaveSubmit" style="margin-left: 5px" v-if="tabCount > currentTabIndex">
                            <em class="fa fa-save pr-2"></em> {{ $t("SaveAndNext") }}</button>
            </div>
        </div>
    </section>
</template>
<script>
import DataService from './../../../../services/AdvanceSetupModuleService'
export default {
    props: {
        tabCount: 0,
        currentTabIndex: 0
    },
    data(){
        return{
            isLoading: false,
            MandatoryString:'Fields marked with an asterisk (*) are mandatory.',
            FormSchema: [{
                    layoutType: "single",
                    Data: [{
                        astype: "TextField",
                        label: this.$t('Functional Area'),
                        name: "FunctionalArea",
                        value: "",
                        validationRules: {
                            "required": true,
                            max: 100
                        },
                    }]
                },
                {
                    layoutType: "single",
                    Data: [{
                        astype: "TextAreaField",
                        label: this.$t('description'),
                        name: "Description",
                        value: "",
                        validationRules: "",
                    }, ]
                }
            ],
            buttons: [],
            companyId: null,
            userId: null,
        }
    },
    created: function () {
        var vm = this;
        vm.userId = vm.GetUserInfo.ID;
        vm.companyId = vm.GetUserInfo.CompanyId;        
    },
    methods:{
        SaveSubmit() {
            var vm = this;
            vm.$refs.FunctionalArea.validateForm.validate().then(result => {
                if(result){
                    var jsonobj = {
                        userId: vm.userId,
                        companyId: vm.companyId,
                        name: vm.FormSchema[0].Data[0].value,
                        description: vm.FormSchema[1].Data[0].value,
                        parentid: 0,
                        id: 0,
                        subModuleCode: "HR_JOB",
                        fileName: '',
                        moduleName: "Hiring",
                    };
                    var params = JSON.stringify(jsonobj);
                    DataService.SaveFunctionalArea(params).then((response) => {                        
                        if (response.data.status == "AlreadyExixts") {                            
                            vm.ShowAlert(vm.$t('AlreadyExists', [vm.$t('FunctionalArea')]), "warning", true, vm.$t("Alert"));
                        }  
                        if (response.data.status != "Success") {
                            vm.ShowAlert(vm.$t('DesignationAlreadyExist', [vm.$t('FunctionalArea')]), "warning", true, vm.$t("Alert"));
                        } else {
                            vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('FunctionalArea')]), "success", true, vm.$t("Alert"));
                        } 
                    });
                }
            });
        },
        Skip: function () {
            var vm = this;
            vm.$parent.NextTab();
        },
        PreviousButton: function () {
            var vm = this;
            vm.$emit("previousTab");
        },
    }
}
</script>